.prev {
  top: 50%;
  left: 20px;
  transform: translateY(-60%);
}

.next {
  top: 50%;
  right: 20px;
  transform: translateY(-60%);
}


.container-dots {
  position: absolute;
  top: 83%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin: 0 5px;
  background: #f1f1f1;
  cursor: pointer;
}

.dot.active {
  background:#090076
}